/*
 * tabbar我的页面
 */
import util, { parGetData, parSetData, checklogin } from '../../common/util';
export default {
	data() {
		return {
			avatarUrl: '../../static/logo.png',
			openid: '',
			token: '',
			userinfo: '',
			appusername: ''
		};
	},
	onShow() {
		this.checkstat();
	},
	methods: {
		toggleavatar: async function() {
			if (!this.token) {
				uni.navigateTo({
					url: '/platforms/app-plus/login/login'
				});
				return false;
			}
			// 更换头像
			var self = this;
			uni.chooseImage({
				count: 1,
				sizeType: 'compressed ',
				success: res => {
					self.image = res.tempFilePaths[0];
					uni.showLoading({
						title: '上传中',
						icon: 'none'
					});
					uni.uploadFile({
						url: util.apiurl + 'syj/index/setavatar', //仅为示例，非真实的接口地址
						filePath: res.tempFilePaths[0],
						name: 'image',
						header: {
							auth: util.httpauth
						},
						success: res => {
							res.data = JSON.parse(res.data);
							if (res.statusCode == 200 && res.data.code < 1) {
								this.$set(self.userinfo, 'avatar', res.data.imageurl);
								// 存入用户信息
								uni.setStorageSync('userinfo', parSetData(self.userinfo));
							} else {
								uni.showToast({
									title: res.data.msg,
									icon: 'none'
								});
							}
						},
						complete: function() {
							uni.hideLoading();
						}
					});
				}
			});
		},
		gologinpage() {
			uni.navigateTo({
				url: '/platforms/app-plus/login/login'
			});
		},
		tougao() {
			uni.switchTab({
				url: '../tougao/tougao'
			});
		},
		gobind() {
			if (this.token && !this.appusername) {
				// 去绑定
				uni.navigateTo({
					url: '/platforms/mp-weixin/login/login'
				});
				return false;
			}
		},
		my() {
			if (!this.token) {
				uni.showToast({
					title: '请先登录',
					icon: 'none'
				});
				return false;
			}
			uni.navigateTo({
				url: '../my/my'
			});
		},
		checkstat() {
			var logininfo = util.checklogin();
			this.openid = logininfo.openid;
			this.userinfo = logininfo.userinfo;
			this.token = logininfo.token;
			this.appusername = logininfo.appusername;
		},
		logout() {
			this.openid = '';
			this.userinfo = '';
			this.token = '';
			this.appusername = '';
			uni.removeStorageSync('wxopenid');
			uni.removeStorageSync('userinfo');
			uni.removeStorageSync('token');
			uni.removeStorageSync('appusername');
		},
		mylike() {
			if (!this.token) {
				uni.showToast({
					title: '请先登录',
					icon: 'none'
				});
				return false;
			}
			uni.navigateTo({
				url: '../list/list'
			});
		},
		login(res) {
			util.logincomm(res, this);
		}
	}
};
